.cabinet {
  @include media-breakpoint-down(md) {
    padding: 0 !important;
  }

  &__inner {
    @media (max-width: 1116px) {
      column-gap: 30px;
    }

    @include media-breakpoint-down(lg) {
      grid-template-columns: 1fr;
    }

    display: grid;
    grid-template-columns: 270px 1fr;
    column-gap: 130px;
  }

  &__sidebar {
    @include media-breakpoint-down(lg) {
      display: flex;
      padding-top: 0;
    }

    @include media-breakpoint-down(md) {
      margin-left: -15px;
      margin-right: -15px;
      padding: 30px 32px;
    }

    padding-top: 50px;
    color: $white;
    background-color: #333541;
  }

  &__content {
    @include media-breakpoint-down(md) {
      padding-top: 0;
    }

    padding-top: 50px;
  }

  &__accordion {
    @include media-breakpoint-down(md) {
      display: block;

      & ~ * {
        // TODO: temporary solution
        display: none;
      }
    }

    display: none;
    margin-left: -15px;
    margin-right: -15px;
  }
}
