.work-history-table {
  $block-name: &;

  margin: 0;

  thead th {
    &:first-child {
      padding-left: 20px;
    }
    &:last-child {
      padding-right: 20px;
    }
  }

  &__item {
    @include media-breakpoint-down(lg) {
      display: grid !important;
      grid-template-columns: repeat(10, 1fr);
      padding: 37px 37px;
      background: #f6f6f6;

      td {
        display: flex !important;
        align-items: center;
        gap: 10px;
        padding-left: 0 !important;
        padding-right: 0 !important;
        padding-top: 10px;
        padding-bottom: 10px;

        &::before {
          min-width: 30px;
        }
      }

      &-move-date {
        grid-column: 1 / 4;
        grid-row: 1 / 3;
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }

      &-cf {
        grid-column: 1 / 3;
      }

      &-lbs {
        grid-column: 3 / 6;
        grid-row: 3 / 4;
        justify-content: center;
      }

      &-moving-from {
        grid-column: 4 / 8;
        grid-row: 1 / 3;
        // justify-content: center;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);

        br {
          display: none;
        }
      }

      &-moving-to {
        grid-column: 8 / -1;
        grid-row: 1 / 3;
        justify-content: flex-end;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);

        br {
          display: none;
        }
      }

      &-miles {
        grid-column: 6 / 9;
        justify-content: center;
      }

      &-estimate {
        grid-column: 9 / -1;
        justify-content: flex-end;
      }
    }

    @include media-breakpoint-down(md) {
      td {
        padding-top: 12px !important;
        padding-bottom: 12px !important;

        &::before {
          width: 100px;
        }
      }

      &-move-date {
        grid-column: 1 / -1;
      }
      &-moving-from {
        grid-column: 1 / -1;
        grid-row: 3 / 4;
        border-bottom: none;
      }
      &-moving-to {
        grid-column: 1 / -1;
        grid-row: 4 / 5;
        justify-content: flex-start;
      }
      &-miles {
        grid-column: 6 / -1;
        grid-row: 5 / 6;
        justify-content: flex-start;
      }
      &-estimate {
        grid-column: 6 / -1;
        justify-content: flex-start;
      }
      &-cf,
      &-lbs {
        grid-column: 1 / 6;

        &::before {
          width: 30px !important;
        }
      }
      &-lbs {
        grid-row: 6 / 7;
        justify-content: flex-start;
      }
      &-miles,
      &-estimate {
        &::before {
          width: 65px !important;
        }
      }
    }

    &-estimate {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      line-height: 19px;
      color: $color-main;

      &::before {
        color: $text-color;
      }
    }

    td {
      &:last-child {
        padding-right: 20px;
      }
    }
  }

  &--cf {
    @include media-breakpoint-down(lg) {
      td {
        padding-top: 13px !important;
        padding-bottom: 13px !important;
      }

      #{$block-name}__item-moving-from {
        grid-column: 1 / -1;
        grid-row: 3 / 4;
        border-bottom: none;
      }
      #{$block-name}__item-moving-to {
        grid-column: 1 / -1;
        grid-row: 4 / 5;
        justify-content: flex-start;
      }
      #{$block-name}__item-cf {
        grid-column: 1 / -1;
        grid-row: 1 / 2;
      }
      #{$block-name}__item-zip-code {
        grid-column: 1 / -1;
        grid-row: 2 / 3;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }
      #{$block-name}__item-contact {
        grid-column: 1 / -1;
      }
    }
    @include media-breakpoint-down(md) {
      #{$block-name}__item-moving-from {
        grid-column: 1 / -1;
      }
      #{$block-name}__item-moving-to {
        grid-column: 1 / -1;
      }
      #{$block-name}__item-cf {
        grid-column: 1 / -1;
      }
      #{$block-name}__item-zip-code {
        grid-column: 1 / -1;
      }
    }
  }
}
