@media (min-width: 320px) and (max-width: 991px) {
  .transportations-table {
    $block-name: &;

    &__item-contact-access-btn {
      &::after {
        right: 25px;
      }
    }

    &__item-moving-from,
    &__item-moving-to {
      br {
        display: none;
      }
    }

    &__item {
      @include media-breakpoint-up(md) {
        grid-template-columns: 35% 1fr min-content !important;
        grid-template-rows: repeat(2, minmax(33px, 1fr));
        row-gap: 0 !important;
        padding: 10px 30px !important;
      }

      display: grid !important;
      grid-template-columns: repeat(2, 1fr);
      row-gap: 10px;
      padding: 30px;
      background-color: $gray-lightest;

      td {
        @include media-breakpoint-up(md) {
          padding: 5px;
        }

        display: grid !important;
        // grid-template-columns: max-content 1fr;
        align-items: center;
        column-gap: 10px;
        padding: 0;

        span {
          white-space: nowrap;
        }

        &::before {
          font-family: 'Gilroy', sans-serif;
          font-weight: 800;
        }
      }

      &-date {
        @include media-breakpoint-up(md) {
          grid-column: span 1 / span 2 !important;
          padding: 10px 0 !important;
        }

        grid-column: 1 / 3;
        grid-template-columns: max-content 1fr;
        grid-row: 1 / span 2 !important;
        padding-bottom: 20px !important;
        padding-left: 0 !important;
        padding-right: 0 !important;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }

      &-moving-from {
        @include media-breakpoint-up(md) {
          grid-column: 2 / 3 !important;
          padding: 10px 0 !important;
        }

        display: grid;
        grid-template-columns: 100px 1fr;
        grid-column: 1 / span 2;
        padding-top: 10px !important;
        padding-bottom: 0 !important;
        padding-left: 0 !important;
        padding-right: 0 !important;
        border-bottom: none;
      }

      &-moving-to {
        @include media-breakpoint-up(md) {
          grid-column: 2 / 3 !important;
          grid-row: 2 / 3 !important;
          padding: 10px 0 !important;
        }

        display: grid;
        grid-template-columns: 100px 1fr;
        grid-column: 1 / span 2;
        padding-bottom: 20px !important;
        padding-left: 0 !important;
        padding-right: 0 !important;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }

      &-cf,
      &-miles {
        padding-top: 10px !important;
        padding: 10px 0 !important;
      }

      &-cf,
      &-lbs,
      &-zip-code {
        grid-template-columns: 70px 1fr;
        padding: 10px 0 !important;
      }

      &-lbs {
        @include media-breakpoint-up(md) {
          grid-column: 3 / 4 !important;
          grid-row: 2 / 3 !important;
          padding: 10px 0 !important;
        }
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }

      &-zip-code {
        @include media-breakpoint-up(md) {
          grid-column: 3 / 4 !important;
          grid-row: 2 / 3 !important;
          padding: 10px 0 !important;
        }
      }

      &-moving-to,
      &-lbs,
      &-zip-code {
        @include media-breakpoint-up(md) {
          padding: 10px 0 !important;
        }
      }

      &-estimate {
        @include media-breakpoint-up(md) {
          grid-column: 1 / 2 !important;
          grid-row: 4 / 5 !important;
          border-bottom: none !important;
          padding: 10px 0 !important;
        }

        padding-left: 0 !important;
        padding-right: 0 !important;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }

      &-miles,
      &-estimate {
        grid-template-columns: 65px 1fr;
      }

      &-contact {
        @include media-breakpoint-up(md) {
          grid-column: 2 / 4 !important;
          grid-row: 3 / 5 !important;
          padding: 10px 0 !important;
        }

        grid-template-columns: 65px 1fr;
        grid-column: 1 / span 2;
        padding-top: 10px !important;
        padding-left: 0 !important;
        padding-right: 0 !important;
      }
    }
  }
}

.transportations-table {
  $block-name: &;

  @include media-breakpoint-up(lg) {
    th,
    td {
      border: none !important;

      &:last-child {
        // width: 265px;
        padding-right: 30px;
      }
    }
  }

  margin: 0;
  margin-bottom: 50px;
  border: none;

  th {
    padding: 20px 0 20px 30px !important;
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    background-color: $gray-lightest;
  }

  &__item {
    &-contact-access-btn {
      position: relative;
      padding-right: 46px;
      padding-top: 0.7em;
      padding-bottom: 0.7em;
      text-transform: uppercase;
      font-family: 'Gilroy', sans-serif;
      font-style: normal;
      font-weight: 800;

      &::after {
        content: '';
        position: absolute;
        z-index: 1;
        right: 25px;
        width: 15px;
        height: 15px;
        background-image: svg-load(
          'down-arrow-icon.svg',
          fill=#fff,
          width=15,
          height=15
        );
        pointer-events: none;
        transform: rotate(-90deg);
        transition: all 0.3s;
      }
    }

    &-actions {
      text-align: right;
    }

    &-estimate {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      line-height: 19px;
      color: $color-main;

      &::before {
        color: $text-color;
      }
    }

    td {
      padding: 20px 0 20px 30px;
      vertical-align: middle;
    }

    &:nth-child(2n) {
      background-color: $gray-lightest;
    }
  }

  &__favorite-btn {
    background-color: red;
    svg {
      path:first-child {
        fill: red;
      }
    }
  }

  &--cf {
    #{$block-name}__item {
      @include media-breakpoint-up(md) {
        grid-template-columns: 1fr 1fr min-content !important;
      }
    }
    #{$block-name}__item #{$block-name}__item-moving-from {
      @include media-breakpoint-down(lg) {
        grid-column: 1 / 2 !important;
      }
      @include media-breakpoint-down(md) {
        grid-column: 1 / -1 !important;
        padding-top: 30px !important;
        border-top: 1px solid rgba(206, 206, 206, 0.5);
      }
    }
    #{$block-name}__item #{$block-name}__item-moving-to {
      @include media-breakpoint-down(lg) {
        grid-column: 1 / 2 !important;
        border-bottom: none;
      }
      @include media-breakpoint-down(md) {
        grid-column: 1 / -1 !important;
        border-bottom: 1px solid rgba(206, 206, 206, 0.5);
      }
    }
    #{$block-name}__item #{$block-name}__item-zip-code {
      @include media-breakpoint-down(lg) {
        grid-column: 2 / 3 !important;
      }
      @include media-breakpoint-down(md) {
        grid-column: 1 / 2 !important;
        grid-row: 2 / 3 !important;
      }
    }
    #{$block-name}__item #{$block-name}__item-contact {
      @include media-breakpoint-down(lg) {
        border-top: 1px solid rgba(206, 206, 206, 0.5);
      }
      @include media-breakpoint-down(md) {
        border-top: none;
      }
      grid-column: 1 / -1 !important;
    }
    #{$block-name}__item #{$block-name}__item-actions {
      @include media-breakpoint-down(lg) {
        grid-row: 1 / 3;
        align-self: center;
        justify-content: flex-end;
        padding: 0;
      }
      @include media-breakpoint-down(md) {
        grid-column: 2 / -1;
      }
    }
    #{$block-name}__item #{$block-name}__item-cf {
      @include media-breakpoint-down(md) {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
      }
    }
  }
}
