.advantages-item {
  &__img-wrap {
    @include media-breakpoint-down(md) {
      margin-bottom: 50px;
    }
    position: relative;
    z-index: 2;

    img {
      @include media-breakpoint-down(lg) {
        max-width: 283px;
      }

      @include media-breakpoint-down(md) {
        max-width: 100%;
      }

      width: 100%;
      object-fit: cover;
    }
  }

  &__content-wrap {
    @include media-breakpoint-down(md) {
      align-items: flex-start;
    }

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
  }

  &__title {
    margin-bottom: 30px;
  }
}
