.credit-card {
  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
    grid-column: auto;
    margin-bottom: 50px;
  }

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column: span 2;
  gap: 30px;

  &__frontside,
  &__backside {
    min-height: 201px;
    padding-bottom: 26px;
    border-radius: 26px;
    background: linear-gradient(66.27deg, #818181 -10.45%, #525252 76.78%);
  }

  &__frontside {
    padding: 30px 18px;
    border-radius: 26px;
    background: linear-gradient(66.27deg, #818181 -10.45%, #525252 76.78%);
  }

  &__frontside-top-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    padding: 0 10px;
  }

  &__frontside-number-wrap {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 6px;
    margin-bottom: 28px;
  }

  &__frontside-bottom-wrap {
    display: grid;
    grid-template-columns: 160px 67px;
    justify-content: space-between;

    .field-text {
      &__input {
        padding: 0 5px !important;
        text-align: left !important;
      }
    }
  }

  &__backside {
    padding-top: 28px;
  }

  &__backside-magnetic-stripe {
    height: 28px;
    margin-bottom: 40px;
    background-color: $black;
  }

  &__backside-cvv-wrap {
    display: grid;
    justify-content: flex-end;
    grid-template-columns: 72px;
    padding: 0 39px;
  }

  .field-text {
    margin-bottom: 0;

    &__name {
      margin-bottom: 4px;
      color: $white;
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
    }

    &__input {
      padding: 2px 2px;
      font-family: 'Gilroy', sans-serif;
      color: $white;
      font-weight: 800;
      text-align: center;
      font-size: 18px;
      line-height: 22px;
      background: rgba(255, 255, 255, 0.5);
    }
  }
}
