.reset-password-form {
  &__title {
    margin-bottom: 15px;
  }
  &__descr {
    margin-bottom: 50px;
  }
  &__fields-wrap {
    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
    }
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    .field-text {
      margin-bottom: 0;
    }

    button[type='submit'] {
      grid-column: 1 / -1;
    }
  }
}
