.advantages {
  &__list-item {
    @include media-breakpoint-down(lg) {
      grid-template-columns: 299px 1fr;
    }

    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
      gap: 0;
      margin-bottom: 50px;
    }

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 58px;

    margin-bottom: 175px;

    /* stylelint-disable */
    &:nth-child(2n) {
      direction: rtl;
      margin-bottom: 0;
    }
  }
}
