.transportation-info {
  display: flex;
  @media (max-width: 1064px) {
    flex-direction: column;
  }
  &__left-side {
    @include media-breakpoint-down(md) {
      padding: 50px 20px;
    }
    max-width: 460px;
    padding: 50px 33px 50px 50px;
    background-color: $gray-lightest;
    @media (max-width: 1064px) {
      max-width: 100%;
      padding-right: 100px;
    }
  }
  &__right-side {
    @include media-breakpoint-down(md) {
      padding: 50px 20px;
    }
    flex-grow: 1;
    padding: 50px;
  }
  &__contacts {
    &-title {
      margin-bottom: 20px;
      line-height: 30px;
    }
    &-list-item {
      @include media-breakpoint-down(md) {
        font-size: 17px;
        line-height: 38px;
      }
      display: flex;
      align-items: center;
      gap: 7px;
      font-size: 18px;
      line-height: 38px;
      color: $color-main;
      &-copy-btn {
        cursor: pointer;
      }
      svg {
        flex-shrink: 0;
        width: 23px;
        fill: $black;
      }
    }
  }
}
