.new-post-block {
  &__content {
    @include media-breakpoint-down(lg) {
      grid-template-columns: 1fr;
    }

    display: grid;
    grid-template-columns: 1fr 269px;
    gap: 30px;
  }

  &__inner {
    padding-top: 50px;
    padding-bottom: 50px;
    border-bottom: 1px solid $gray-lighter;
  }
}
