.page-footer-list {
  @include media-breakpoint-down(lg) {
    grid-template-columns: 1fr;
    column-gap: 74px;
    font-size: 14px;
    line-height: 34px;
  }
  @include media-breakpoint-down(md) {
    grid-template-columns: max-content;
    column-gap: 80px;
  }

  display: grid;
  column-gap: 130px;
  row-gap: 10px;
  grid-template-rows: repeat(5, 1fr);
  grid-auto-flow: column dense;

  color: rgba(#fff, 0.8);
  font-weight: 400;
  font-size: 18px;
  line-height: 38px;

  a {
    color: $white;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

    &:focus {
      box-shadow: 0px 0px 0px 3px rgba($white, 0.1);
    }
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &--contacts {
    grid-template-rows: auto;
    grid-auto-flow: row;
  }
}
