.pricing-details {
  &__title {
    @include media-breakpoint-down(md) {
      br {
        display: none;
      }
    }
    margin-bottom: 50px;
    text-align: center;
  }

  &__price {
    font-weight: 900;
    line-height: 50px;
    color: $color-main;
  }

  &__info {
    @include media-breakpoint-down(lg) {
      grid-template-columns: 1fr 300px;
    }

    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
      padding-bottom: 50px;
    }

    display: grid;
    align-items: center;
    grid-template-columns: 1fr 332px;
    gap: 10px;
    border: 5px solid $color-main;

    h3 {
      margin-bottom: 20px;
    }

    .button {
      max-width: 324px;
      margin-bottom: 35px;
    }

    p {
      margin-bottom: 25px;
    }

    ul {
      margin-top: 25px;
    }

    ul li {
      position: relative;
      margin-bottom: 25px;
      padding-left: 33px;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 20px;
        height: 20px;
        transform: translateY(-50%);
        background: svg-load('checked-icon.svg') center no-repeat;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__left-wrap {
    @include media-breakpoint-down(lg) {
      padding: 30px;
    }

    padding: 50px;
  }

  &__right-wrap {
    display: flex;
    justify-content: center;
  }
}
