.subscribe-section {
  &__inner {
    @include media-breakpoint-down(lg) {
      grid-template-columns: 1fr;
      gap: 30px;
    }

    @include media-breakpoint-down(md) {
      padding-top: 30px;
      padding-bottom: 30px;
    }

    display: grid;
    align-items: center;
    grid-template-columns: 1fr 390px;
    column-gap: 42px;
    padding: 20px 37px;
    background: linear-gradient(93.24deg, #276ef1 18.98%, #1852bd 86.26%);
  }

  &__title {
    @include media-breakpoint-down(md) {
      font-size: 33px;
    }

    margin-bottom: 0;
    color: $white;
    font-weight: 300;
    font-size: 36px;
    line-height: 46px;
  }

  &__form {
    @include media-breakpoint-down(md) {
      flex-direction: column;
      gap: 15px;
    }

    display: flex;
  }

  &__form-field-text {
    @include media-breakpoint-down(md) {
      width: auto;
    }

    width: 50%;
    margin: 0;
    border: 1px solid $white;

    input {
      padding-left: 20px;
      padding-right: 20px;
      color: $white;
      background-color: transparent;

      &::placeholder {
        color: $white !important;
      }
    }
  }

  &__form-submit-btn {
    @include media-breakpoint-down(md) {
      width: auto;
    }

    width: 50%;
    color: $black;
    background-color: $white;

    &:hover {
      color: $white;
      background-color: $black;
    }

    &:focus {
      background-color: $white;
      box-shadow: 0px 0px 0px 4px $black;
    }
  }
}
