.link-add-form {
  @include media-breakpoint-down(lg) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 30px;
  }

  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
    row-gap: 10px;
  }

  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px;
  color: $white;
  background: radial-gradient(
    93.06% 93.06% at 50% 50%,
    #276ef1 0%,
    #1852bd 100%
  );

  &__descr {
    @include media-breakpoint-down(lg) {
      grid-column: span 2;
      margin-bottom: 0;
      font-size: 30px;
      line-height: 30px;
    }

    @include media-breakpoint-down(md) {
      grid-column: auto;
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 27px;
    }

    margin-bottom: 20px;
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 18px;
    line-height: 27px;
  }

  button[type='submit'] {
    @include media-breakpoint-down(lg) {
      height: 40px;
    }

    color: $color-main;
    background-color: $white;
  }

  .field-text {
    @include media-breakpoint-down(lg) {
      margin: 0;

      &__name {
        display: none;
      }

      &__input {
        height: 40px;
      }
    }

    @include media-breakpoint-down(md) {
      &__name {
        display: block;
      }
    }

    &__name {
      color: $white;
    }

    &__input {
      border: 1px solid $white;
      padding: 8px 15px;
      color: $white;
      background-color: transparent;

      &::placeholder {
        color: $white;
      }
    }
  }
}
