.accordion {
  $block: &;

  &__item {
    overflow: hidden;
    margin-bottom: 0;
    user-select: none;
    border-bottom: 1px solid #4d4f5b;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item--opened {
    & #{$block}__title {
      background-color: #5c5d67;

      &::after {
        top: 48px;
        transform: rotate(180deg);
        background-image: svg-load('down-arrow-icon.svg', fill=#fff);
      }
    }

    & #{$block}__content {
      display: block;
    }
  }

  &__title {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    column-gap: 18px;
    padding: 25px 34px;
    margin-bottom: 0;
    padding-right: 40px;
    color: $white;
    font-weight: 500;
    font-size: 21px;
    line-height: 25px;
    cursor: pointer;
    background-color: #333541;

    &::after {
      content: '';
      position: absolute;
      top: 23px;
      right: 28px;
      width: 30px;
      height: 30px;
      transform-origin: top;
      transition: 0.2s;
      background-image: svg-load('down-arrow-icon.svg', fill=#fff);
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__content {
    display: none;
    padding: 50px 15px;
  }
}
