.cabinet-navigation {
  @include media-breakpoint-down(lg) {
    width: auto;
    margin-left: -15px;
    margin-right: -15px;
  }

  width: 100%;

  &__list {
    @include media-breakpoint-down(lg) {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      align-items: center;
    }

    @include media-breakpoint-down(md) {
      display: block;
    }
  }

  &__list-item {
    @include media-breakpoint-down(md) {
      display: none;
    }

    &:not(:last-child) {
      border-right: 1px solid #4d4f5b;
    }

    background-color: #333541;
  }

  &__list-item--logout {
    @include media-breakpoint-down(lg) {
      height: 100%;
      margin-top: 0;
    }

    @include media-breakpoint-down(md) {
      display: block;

      a {
        flex-direction: row;
        padding-top: 24px;
        padding-bottom: 24px;
      }
    }

    margin-top: 190px;

    a {
      @include media-breakpoint-down(lg) {
        border: none;
        height: 100%;
      }

      padding: 14px;
      border-top: 1px solid #4d4f5b;
      color: #e54937;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      justify-content: center;

      &:hover {
        color: $white;
      }

      &:focus {
        color: #e54937;
        box-shadow: 0px 0px 0px 4px rgba(229, 73, 55, 0.5);
      }
    }
  }

  &__list-item-link {
    @include media-breakpoint-down(lg) {
      row-gap: 20px;
      flex-direction: column;
      border: none;
      text-align: center;

      br {
        display: block;
      }
    }

    display: flex;
    align-items: center;
    padding: 20px 32px;
    column-gap: 20px;
    border-bottom: 1px solid #4d4f5b;
    color: $white;
    font-weight: 500;
    line-height: 16px;
    text-decoration: none;

    br {
      display: none;
    }

    svg {
      width: 24px;
      height: 24px;
    }

    &:hover {
      color: $white;
      background-color: #5c5d67;
    }

    &:focus {
      color: inherit;
      background-color: $black;
      box-shadow: none;
    }

    &:active {
      color: rgba($white, 0.5);
      background-color: rgba($white, 0.2);
    }

    &--active {
      background-color: #5c5d67;
    }
  }
}
