.directions-filter {
  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px;
    background-color: $gray-lightest;
    cursor: pointer;
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    font-size: 14px;
    line-height: 14px;

    svg {
      stroke: currentColor;
    }

    &:hover {
      color: $white;
      background-color: $color-main;
    }
  }
}
