.user-edit-form {
  &__top-wrap {
    display: flex;
    align-items: center;
    column-gap: 30px;
    margin-bottom: 30px;
  }

  &__avatar-file-upload {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 64px;
    border-radius: 1000px;
    color: $white;
    cursor: pointer;
    background-color: $color-main;
  }

  &__fullname {
    display: flex;
    column-gap: 15px;
    margin-bottom: 4px;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
  }

  &__edit-btn {
    cursor: pointer;
  }

  &__fields-wrap {
    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
    }

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 25px 30px;

    .field-text {
      margin-bottom: 0;
    }

    button[type='submit'] {
      align-self: end;
    }
  }

  input[type='file'] {
    display: none;
  }
}
