.top-section {
  @include media-breakpoint-down(lg) {
    background: $white url('../images/top-section/background--cropped.png')
      no-repeat;
    background-size: cover;
  }

  @include media-breakpoint-down(md) {
    border-bottom: 1px solid #e9e9ea;
    background: $white;
  }

  background: $white url('../images/top-section/background.jpg') no-repeat;
  background-size: cover;

  &__inner {
    @include media-breakpoint-down(md) {
      min-height: auto;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;

    min-height: calc(100vh - $header-height);
  }

  &__tabs {
    @include media-breakpoint-down(md) {
      max-width: 100%;
    }

    max-width: 530px;

    background-color: #fff;
  }
}
