.transportations {
  $block-name: &;

  &__title {
    margin-bottom: 50px;
    padding-bottom: 20px;
    border-bottom: 1px solid $gray-lighter;
  }

  &__sort-list {
    @include media-breakpoint-down(lg) {
      grid-template-columns: repeat(4, 1fr);
    }

    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
      padding-bottom: 30px;
    }

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 18px;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid $gray-lighter;
  }

  &__sort-list-item,
  &__sorts-reset-btn {
    height: 46px;
  }

  &__chips-and-link-wrap {
    @include media-breakpoint-down(lg) {
      flex-direction: column;
      align-items: stretch;
      row-gap: 20px;
    }

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__turn-on-notifications-link {
    color: $color-main;
    font-weight: 900;
    font-size: 12px;
    line-height: 15px;
    text-decoration-line: underline;
  }

  &__sorts-reset-btn {
    @include media-breakpoint-down(lg) {
      grid-column: 1 / -1;
    }
  }

  &__pagination-wrap {
    display: flex;
    justify-content: center;
  }

  &__pagination {
    margin: 0;
  }

  &--cubic-feets {
    #{$block-name}__sorts-reset-btn {
      @include media-breakpoint-down(lg) {
        grid-column: 2 / -1;
      }

      @include media-breakpoint-down(md) {
        grid-column: auto;
      }

      grid-column: -2;
    }

    #{$block-name}__sort-list-item {
      @include media-breakpoint-down(lg) {
        grid-column: span 1;
      }

      @include media-breakpoint-down(md) {
        grid-column: auto;
      }

      grid-column: -3;
    }
  }
}
