.chips {
  @include media-breakpoint-down(lg) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  @include media-breakpoint-down(md) {
    grid-template-columns: repeat(2, 1fr);
  }

  display: flex;
  column-gap: 15px;

  &__item {
    @include media-breakpoint-down(lg) {
      justify-content: center;
      column-gap: 16px;
      padding-top: 12px;
      padding-bottom: 12px;

      & > svg {
        width: 21px;
        height: 12px;
      }

      button svg {
        height: 12px;
      }
    }

    position: relative;
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 5px 15px;
    padding-right: 30px;
    background-color: $gray-lightest;
  }

  &__item-key,
  &__item-value {
    @include media-breakpoint-down(lg) {
      font-size: 21px;
      line-height: 19px;
    }

    font-size: 12px;
    line-height: 19px;
  }

  &__item-delete-button {
    @include media-breakpoint-down(lg) {
      top: 9px;
      right: 9px;
    }

    position: absolute;
    right: 5px;
    top: 5px;
    display: flex;
    cursor: pointer;

    svg {
      fill: $gray-light;
    }

    &:hover svg {
      fill: $black;
    }
  }
}
