.new-post-links {
  $block-name: &;

  @include media-breakpoint-down(md) {
    display: block;
  }

  display: flex;

  &__item-link {
    @include media-breakpoint-down(md) {
      padding-top: 30px;
      padding-bottom: 30px;
      padding-left: 15px;
      padding-right: 15px;
      color: $white;
      font-size: 28px;
      line-height: 27px;
      background-color: $gray-light;
    }

    display: block;
    padding: 20px 50px;
    font-family: 'Gilroy', sans-serif;
    color: $black;
    text-decoration: none;
    font-weight: 800;
    font-size: 18px;
    line-height: 27px;
  }

  #{$block-name}__item-link--active {
    color: $white;
    background-color: $color-main;
  }
}
