//        - main-nav,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-nav {
  $block-name: &; // #{$block-name}__element

  position: relative;
  z-index: 10;

  //  
  &__list {
    @include media-breakpoint-down(md) {
      .js & {
        padding-top: 20px;

        &--lvl-2 {
          padding-top: 0;
        }
      }
    }

    margin: 0;
    padding: 0;
    list-style: none;

    //   
    @media (max-width: ($menu-desktop-width)) {
      // /      JS

      .js & {
        overflow: hidden;
        padding-top: 100px;

        &--lvl-2 {
          max-height: none;
          padding-top: 0;
        }
      }

      .js #{$block-name}--open & {
        max-height: 1000px;
        transition: max-height 0.6s;

        &--lvl-2 {
          max-height: none;
        }
      }
    }

    //   
    @media (min-width: $menu-desktop-width) {
      display: flex;
      flex-wrap: wrap;

      &--lvl-2 {
        display: block;
      }

      &--lvl-3 {
        display: block;
      }
    }
  }

  &__item {
    position: relative;

    li {
      padding: 0;
    }

    &--active {
      & > #{$block-name}__link {
        // stylelint-disable-line  selector-combinator-blacklist
        color: #000;
      }
    }

    @media (min-width: $menu-desktop-width) {
      padding: 14px 0;

      &--has-child {
        &:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: 29px;
          right: 10px;
          width: 10px;
          height: 6px;
          background-image: svg-load(
            'down-arrow-icon.svg',
            fill=#fff,
            width=10,
            height=6
          );
          transition: all 0.3s;
        }

        & > a {
          padding-right: 30px;
        }

        &:hover {
          & > a {
            background-color: $gray-darken;
          }
        }
      }
    }
  }

  &__link {
    @include media-breakpoint-down(md) {
      font-size: 25px;
      line-height: 29px;
    }

    position: relative;
    display: block;
    padding: 0.5em 1em;
    border-radius: 1000px;
    font-weight: 500;
    color: $white;
    text-decoration: none;

    &:hover,
    &:focus {
      z-index: 1;
      color: $white;
      text-decoration: none;
      box-shadow: none;
    }

    @media (min-width: $menu-desktop-width) {
      &:focus {
        background-color: $gray-darken;
        border-radius: 1000px;
        box-shadow: 0px 0px 0px 3px rgba($white, 0.1);
      }

      &:hover {
        background-color: $gray-darken;
      }
    }

    @media (max-width: $menu-desktop-width) {
      font-size: 50px;
      line-height: 59px;
    }

    &--lvl-2 {
      @include media-breakpoint-down(md) {
        padding-left: 2em;
      }

      min-width: 200px;
      border-radius: inherit !important;

      &:focus {
        border-radius: 0;
        box-shadow: 0px 0px 0px 4px rgba(39, 110, 241, 0.5);

        @media (max-width: ($menu-desktop-width)) {
          box-shadow: none;
        }
      }

      @media (max-width: ($menu-desktop-width)) {
        padding-left: 3em;
        padding-top: 0;
        padding-bottom: 0;
        font-size: 30px;
        color: rgba($white, 0.5);
      }
    }

    &--lvl-3 {
      min-width: 200px;
      border-radius: inherit;

      &:focus {
        border-radius: 0;
      }

      @media (max-width: ($menu-desktop-width)) {
        padding-left: 3em;
      }
    }
  }

  &__sublist-wrapper {
    &--lvl-2 {
      @media (min-width: $menu-desktop-width) {
        position: absolute;
        z-index: 1;
        top: 100%;
        left: 0;
        height: 0;
        opacity: 0;
        background-color: $gray-darken;
        overflow: hidden;
        transition: opacity $transition-time;

        #{$block-name}__link {
          background-color: #4c4c4c;

          &:hover {
            background-color: $gray;
          }
        }

        // stylelint-disable
        #{$block-name}__item--show-child > &,
        #{$block-name}__item:hover & {
          height: auto;
          overflow: visible;
          opacity: 1;
        }
        // stylelint-enable
      }
    }

    &--lvl-3 {
      @media (min-width: $menu-desktop-width) {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 100%;
        opacity: 0;
        height: 0;
        overflow: hidden;
        background-color: $gray-darken;
        transition: opacity $transition-time;

        // stylelint-disable
        #{$block-name}__item--show-child > &,
        #{$block-name}__item--lvl-2:hover & {
          height: auto;
          overflow: visible;
          opacity: 1;
        }
        // stylelint-enable
      }
    }
  }

  // 

  &__toggler {
    display: inline-block;
    border: none;
    padding: 0;
    background: none;

    @media (min-width: $menu-desktop-width) {
      display: none;
    }

    .no-js & {
      display: none;
    }
  }
}
