.user-nav {
  &__info-wrap {
    display: flex;
    align-items: center;
    padding-right: 15px;
    cursor: pointer;
    transition: background-color $transition-time;
    border-radius: $border-radius;

    &:hover {
      background-color: $gray;
    }
  }

  &__thumbnail {
    margin-right: 12px;
    border: 2px solid $white;
    border-radius: 100%;
  }

  &__name {
    margin-right: 10px;
    font-weight: 500;
    font-size: 22px;
    line-height: 26px;
  }

  &__list {
    display: none;
  }
}
