.auth-block {
  @include media-breakpoint-down(lg) {
    margin-left: auto;
    margin-right: 50px;
  }

  @include media-breakpoint-down(md) {
    margin-right: 0;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba($white, 0.4);
  }

  display: flex;
  justify-content: space-between;
  column-gap: 25px;

  &__link {
    @include media-breakpoint-down(md) {
      font-size: 17px;
      line-height: 20px;

      svg {
        width: 16px;
        height: 19px;
      }
    }

    display: flex;
    align-items: center;
    column-gap: 17px;

    padding: 0.5em 1em;
    border-radius: 1000px;

    color: $white;
    text-decoration: none;

    svg {
      width: 12px;
      height: 14px;
    }

    &:hover {
      color: inherit;
      text-decoration: underline;
    }

    &:focus {
      color: inherit;
      border-radius: 1000px;
    }

    &--accent {
      @include media-breakpoint-down(md) {
        font-size: 19px;
        line-height: 22px;
      }

      padding-right: 1.5em;
      padding-left: 1.5em;

      color: $black;

      background-color: #fff;

      &:hover {
        color: $white;
        text-decoration: none;

        background-color: $color-main;
      }

      &:focus {
        color: $black;
      }
    }
  }
}
