.about-details {
  &__inner {
    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
    }

    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
  }

  &__content-wrap {
    @include media-breakpoint-down(md) {
      padding-top: 0;
    }

    padding-top: 150px;

    p {
      margin-bottom: 30px;
    }
  }

  &__title {
    margin-bottom: 50px;
  }

  &__main-img-wrap {
    @include media-breakpoint-down(md) {
      margin-bottom: 50px;
    }
  }

  &__main-img {
    @include media-breakpoint-down(md) {
      width: 100%;
      min-height: 300px;
      object-fit: cover;
    }
  }
}
