.social {
  $block-name: &; // #{$block-name}__

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: $typo-margin-vertical;
  margin-bottom: $typo-margin-vertical;
  padding: 0;
  list-style: none;

  &__link-wrap {
    margin-right: 20px;
  }

  &__link {
    display: block;
    padding: 5px;
    border-radius: 100%;
    background-color: #dadada;
    transition: background-color $transition-time;

    #{$block-name}__link-wrap--telegram:hover &,
    #{$block-name}__link-wrap--telegram &:focus {
      background-color: $color-telegram;
    }

    #{$block-name}__link-wrap--whatsapp:hover &,
    #{$block-name}__link-wrap--whatsapp &:focus {
      background-color: $color-whatsapp;
    }

    // #{$block-name}__link-wrap--vk & {
    //   background-color: $color-vk;
    // }

    // #{$block-name}__link-wrap--tw & {
    //   background-color: $color-tw;
    // }

    // #{$block-name}__link-wrap--gh & {
    //   background-color: $color-gh;
    // }

    svg {
      display: block;
      width: 16px;
      height: 16px;
      fill: $black;
    }

    &:focus {
      border-radius: 1000px;
    }
  }

  &__text {
    @include hide-text;
  }
}
