//        - modal,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.modal {
  $block-name: &; // #{$block-name}__element
  display: none;

  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;

  &__dialog {
    display: flex;
    align-items: center;
    position: relative;
    width: auto;
    max-width: 539px;
    min-height: calc(
      100% - 4em
    ); // ,  margin,   
    margin: 2em auto;
    padding: 0 15px;
    pointer-events: none;
  }

  &__content {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    min-height: 40px;
    padding: 47px;
    pointer-events: auto;
    background-color: $white;
    outline: 0;

    @include media-breakpoint-down(md) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__header {
    flex-shrink: 0;
    position: relative;
  }

  &__title {
    margin-bottom: 20px;
    font-size: $font-size-h3;
  }

  &__close {
    position: absolute;
    top: 15px;
    right: 15px;
    outline: 0;
  }

  &__body {
    flex: 1 1 auto;
    position: relative;
  }

  &__footer {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
    align-items: flex-start;
    justify-content: flex-end;
    padding: 16px;
    border-top: 1px solid $gray-lighten;

    /* stylelint-disable */
    > * {
      margin-left: 8px;
    }
  }
  /* stylelint-endble */

  &--show {
    #{$block-name}__dialog {
      animation: modal-dialog 0.3s 1 ease-out forwards;
    }
  }

  &--register-form {
    #{$block-name}__dialog {
      max-width: 848px;
    }
  }

  &--login-form {
    #{$block-name}__dialog {
      max-width: 450px;
    }
  }

  &--transportation-info {
    #{$block-name}__dialog {
      max-width: 1100px;
    }
    #{$block-name}__content {
      padding: 0;
    }
  }
}

.modal-open {
  overflow: hidden;

  .modal {
    overflow-x: hidden;
    overflow-y: auto;
  }
}

.modal-backdrop {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  animation: modal-backdrop 0.3s 1 ease-out forwards;
}

@keyframes modal-backdrop {
  from {
    background: rgba(0, 0, 0, 0);
    backdrop-filter: blur(0);
  }

  to {
    background: rgba(0, 0, 0, 0.75);
    backdrop-filter: blur(12px);
  }
}

@keyframes modal-dialog {
  from {
    transform: translate(0, -2em);
  }

  to {
    transform: translate(0, 0);
  }
}
