//        - breadcrumbs,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.breadcrumbs {
  list-style: none;
  margin-top: $typo-margin-vertical;
  margin-bottom: $typo-margin-vertical;
  padding: 0;
  color: $gray-light;

  a {
    text-decoration: none;
  }

  &__item {
    display: inline-block;
    margin-right: 0.6em;
    white-space: nowrap;

    &:not(:last-child):after {
      content: '';
      display: inline-block;
      margin-left: 0.8em;
      color: $gray;
      width: 12px;
      height: 7px;
      background-image: svg-load(
        'right-arrow-icon.svg',
        fill=$gray-light,
        width=12,
        height=7
      );
    }
  }
}
