.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  column-gap: 20px;
  max-width: 100%;
  overflow: hidden;
  border: none;
  padding: 0.7em 1.5em;
  transition: background-color $transition-time, color $transition-time;
  color: $white;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  text-transform: none;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  text-align: center;
  background: $black;
  cursor: pointer;

  svg {
    flex-shrink: 0;
  }
}

.button:hover {
  background-color: $gray-light;
}

.button:focus {
  border-color: $border-color;
  box-shadow: 0px 0px 0px 4px rgba(39, 110, 241, 0.5);
}

.button:focus-visible {
  outline: none;
}

.button:active {
  color: $text-color-muted;
  background-color: $gray-light;
}

.button--full-width {
  width: 100%;
}

.button--transparent {
  color: $gray-light;
  background-color: transparent;

  &:hover,
  &:focus {
    color: $black;
    background-color: transparent;
  }
}

.button--icon {
  @include media-breakpoint-down(lg) {
    border: 1px solid $gray-light;
  }
  width: 40px;
  height: 40px;
  padding: 11px 10px;
  color: transparent;
  background-color: transparent;

  &:hover {
    background-color: transparent;
    svg {
      fill: $color-main;
    }
  }

  &:focus {
    background-color: transparent;
  }
}

.button--social {
  position: relative;
  height: 38px;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 30px;
  div {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 38px;
    border: 1px solid $color-main;
    background-color: $white;
  }
  span {
    margin-left: 38px;
  }
}

.button--primary {
  background-color: $color-main;

  &:hover {
    color: $white;
    background-color: darken($color-main, 20%);
  }

  &:focus {
    color: $white;
    background-color: $color-main;
    box-shadow: 0px 0px 0px 4px rgba(0, 0, 0, 0.75);
  }

  &:active {
    background-color: darken($color-main, 20%);
  }
}

.button--canceled {
  color: #e54937;
  background-color: #e5e5e5;

  &:hover {
    color: $white;
    background-color: #e54937;
  }

  &:focus {
    border-color: transparent;
    background: #e5e5e5;
    box-shadow: 0px 0px 0px 4px rgba(229, 73, 55, 0.5);
  }

  &:active {
    color: $text-color-muted;
    background-color: #e54937;
  }
}
