.video-preview {
  padding: 75px 0;
  background-color: $gray-lightest;

  &__inner {
    @include media-breakpoint-down(md) {
      flex-direction: column;
    }

    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__left-wrap {
    @include media-breakpoint-down(lg) {
      max-width: 100%;
    }

    max-width: 470px;
  }

  &__link {
    position: relative;
    display: block;
    padding-bottom: 38px;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -57%);
      width: 202px;
      height: 206px;
      fill: $white;
      transition: fill $transition-time;
    }

    &:hover,
    &:focus {
      svg {
        fill: $color-main;
      }
    }
  }

  &__link-text {
    position: absolute;
    right: -6px;
    bottom: 0;
    color: $black;
    line-height: 19px;
  }
}
