.user-info {
  @include media-breakpoint-down(lg) {
    display: none;
  }

  @include media-breakpoint-down(md) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    column-gap: 20px;
    margin-bottom: 0;
    height: 193px;
  }

  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;

  &__avatar {
    @include media-breakpoint-down(md) {
      width: 120px;
      height: 120px;
      margin: 36px 0;
    }

    margin-bottom: 20px;
    border: 4px solid $white;
    border-radius: 1000px;
  }

  &__fullname {
    @include media-breakpoint-down(md) {
      font-size: 20px;
      line-height: 23px;
    }

    margin-bottom: 4px;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
  }

  &__phone {
    @include media-breakpoint-down(md) {
      font-size: 17px;
      line-height: 20px;
    }
  }
}
