//        - table-responsive,  ,
// , , , $media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.table-responsive {
  $screen-lg: 991px;

  width: 100%;
  margin-top: $typo-margin-vertical;
  margin-bottom: $typo-margin-vertical;
  border-collapse: collapse;
  // border: 1px solid $border-color;

  caption {
    caption-side: top;
    text-align: left;
    border: 1px solid $border-color;
    border-bottom: 0;
    padding: 0.4em $field-padding-horizontal;
    font-size: $font-size-sm;
    line-height: 1em;
    color: $text-color-muted;

    @media (max-width: ($screen-lg - 1)) {
      display: block;
      margin-top: 1em;
      margin-bottom: 0.5em;
      border: 0;
      padding: 0;
      font-family: $font-family;
      font-size: $font-size-h3;
      font-weight: 700;
      line-height: 1.2;
      color: currentColor;
    }
  }

  td,
  th {
    padding: $field-padding-vertical $field-padding-horizontal;
    vertical-align: top;
    // border-bottom: 1px solid $border-color;

    &:not(:first-child) {
      // border-left: 1px solid $border-color;
    }
  }

  th {
    text-align: inherit;
    padding: 20px 0 20px 30px;
    font-family: 'Gilroy', sans-serif;
    font-weight: 800;
    background-color: $gray-lightest;

    &:last-child {
      padding-right: 30px;
    }
  }

  tbody {
    tr {
      &:nth-child(2n) {
        background-color: $gray-lightest;
      }
    }

    td {
      padding: 20px 0 20px 30px;
      vertical-align: middle;

      &:last-child {
        padding-right: 30px;
      }
    }
  }

  @media (max-width: ($screen-lg - 1)) {
    display: block;
    border: 0;

    thead {
      display: none;
    }

    tbody {
      display: block;
      width: 100%;

      tr {
        display: block;
        // border: 1px solid $border-color;
        border-bottom: 0;

        &:not(:last-child) {
          // stylelint-disable-line  max-nesting-depth
          margin-bottom: 1em * $line-height;
        }
      }

      td {
        display: block;
        width: 100%;

        &:before {
          // stylelint-disable-line  max-nesting-depth
          content: attr(data-label);
          display: block;
          font-weight: 700;
        }
      }
    }
  }
}
