.login-form {
  &__field-wrap {
    position: relative;
  }
  &__reset-password-btn,
  &__remind-me-btn {
    position: absolute;
    top: 0;
    right: 0;
    color: $color-main;
    cursor: pointer;

    &:hover {
      color: $link-color--hover;
    }
  }
  &__submit-btn {
    margin-bottom: 17px;
    height: 50px;
    text-transform: uppercase;
  }
  &__register-link-wrap {
    color: $gray;
  }
  &__divider {
    position: relative;
    color: $gray;
    text-align: center;
    margin-bottom: 30px;

    span {
      position: relative;
      z-index: 1;
      padding: 0 5%;
      background-color: $white;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      left: 0;
      height: 1px;
      transform: translateY(-50%);
      background-color: $gray-light;
    }
  }
  &__social-btn {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
