.reservation-question-block {
  &__title {
    @include media-breakpoint-down(md) {
      font-size: 32px;
      line-height: 32px;
    }
    margin-bottom: 20px;
    font-size: 34px;
    line-height: 34px;
  }

  p {
    @include media-breakpoint-down(md) {
      max-width: 100%;
    }
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba($black, 0.15);
    color: $color-main;
    @media (max-width: 1064px) {
      max-width: 380px;
    }
  }

  h4 {
    margin-bottom: 10px;
    line-height: 44px;
  }

  &__confirm-btn {
    margin-right: 15px;
    padding-left: 3.3em;
    padding-right: 3.3em;
  }

  &__reject-btn {
    padding-left: 3.2em;
    padding-right: 3.2em;
  }
}
