// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.pagination {
  display: flex;
  flex-wrap: wrap;
  column-gap: 5px;
  margin-top: $typo-margin-vertical;
  margin-bottom: $typo-margin-vertical;

  &__item {
    display: block;
    min-width: 2em;
    border: 1px solid $gray-lighter;
    padding: 0.5em 1em;
    text-decoration: none;
    text-align: center;
    color: $color-main;

    &:hover,
    &:focus {
      text-decoration: none;
    }

    @at-root a#{&} {
      &:hover,
      &:focus {
        border-color: $color-main;
      }
    }

    @at-root span#{&} {
      cursor: default;
    }

    &--active {
      color: $white;
      background-color: $color-main;
    }
  }

  &--centered {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
