.transportation-props {
  @include media-breakpoint-down(md) {
    flex-direction: column;
  }

  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 15px;
  margin-bottom: 20px;
  padding-bottom: 30px;
  border-bottom: 1px solid $gray-lighter;

  &__list {
    @include media-breakpoint-down(md) {
      margin-bottom: 30px;
    }
    min-width: 276px;
    padding: 30px 20px;
    background-color: $gray-lightest;
  }
  &__coordinates {
    position: relative;
    margin-bottom: 20px;
    padding-left: 30px;

    a {
      display: inline-block;
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      font-size: 16px;
      line-height: 30px;
    }

    span {
      font-size: 16px;
      line-height: 30px;
      color: $gray;
    }

    &-from,
    &-to {
      span {
        display: inline-block;
        width: 70%;
      }
    }

    &-from {
      margin-bottom: 12px;
    }

    svg {
      position: absolute;
      top: 50%;
      bottom: 0;
      left: 10px;
      transform: translateY(-50%);
    }
  }
  &__tags {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
    &-item {
      padding: 0 10px;
      color: $white;
      font-size: 12px;
      line-height: 30px;
      background-color: $black;
    }
  }
  &__date {
    display: flex;
    align-items: center;
    column-gap: 20px;
    &-key {
    }
    &-value {
      color: $color-main;
      font-size: 14px;
      line-height: 14px;
    }
  }
  &__accent-value {
    b {
      display: block;
      margin-bottom: 18px;
      font-size: 24px;
      line-height: 19px;
    }
    span {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      font-size: 43px;
      line-height: 19px;
      color: $color-main;
    }
  }
}
