.post-add-form {
  @include media-breakpoint-down(md) {
    padding: 30px 15px;
  }

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-auto-flow: row;
  gap: 27px;
  padding: 50px 45px;
  background-color: $gray-lightest;

  button[type='submit'] {
    @include media-breakpoint-down(md) {
      grid-column: span 1;
      height: 50px;
    }

    align-self: flex-end;
    grid-column: span 2;
    width: 100%;
    height: 40px;
  }

  .field-text {
    margin: 0;

    &__input {
      @include media-breakpoint-down(md) {
        height: 50px;
      }

      height: 40px;
      background-color: $white;
      border: 1px solid rgba($gray-light, 0.5);
    }
  }
}
