.mobile-menu {
  $component-name: &;

  position: fixed;
  z-index: 20;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  color: #fff;
  font-weight: 500;

  pointer-events: none;

  &__backdrop {
    height: 100%;
    cursor: pointer;
  }

  &__inner {
    @include media-breakpoint-down(md) {
      max-width: 325px;
      padding-left: 25px;
      padding-right: 25px;
      padding-top: $header-height + 30px;
    }

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    max-width: 475px;
    max-height: 100vh;
    overflow: auto;
    height: 100%;
    padding: $header-height 50px;
    transform: translateX(475px);

    background-color: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(15px);
    transition: transform 0.3s;
  }

  &--opened {
    @media (max-width: $menu-desktop-width) {
      pointer-events: all;

      #{$component-name}__inner {
        transform: translateX(0);
      }
    }
  }
}
