.register-form {
  @include media-breakpoint-down(lg) {
    column-gap: 30px;
  }
  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
  }
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
  &__social {
    @include media-breakpoint-down(lg) {
      flex-direction: column;
      align-items: normal;
      margin-bottom: 30px;
    }
    grid-column: 1 / -1;
    display: flex;
    gap: 30px;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 42px;
    margin-top: 20px;

    &-info {
      h3 {
        font-size: 24px;
        line-height: 50px;
      }
      p {
        max-width: 250px;
        margin-bottom: 0;
        line-height: 19px;
      }
    }

    &-btns {
      @include media-breakpoint-down(lg) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
      }
      @include media-breakpoint-down(md) {
        grid-template-columns: 1fr;
      }
      display: flex;
      flex-grow: 1;
      column-gap: 15px;
    }
  }
  &__social-btn {
    @include media-breakpoint-down(lg) {
      max-width: 100%;
    }
    max-width: 210px;
  }
  &__submit-btn {
    grid-column: 1 / -1;
    margin-bottom: 17px;
    height: 50px;
    text-transform: uppercase;
  }
}
