.new-review-add-form {
  &__inner {
    @include media-breakpoint-down(lg) {
      padding: 0;
    }

    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr !important;
      gap: 20px;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    display: grid;
    column-gap: 30px;
    grid-template-columns: 1fr min-content min-content;
    align-items: flex-end;
    padding: 0 100px;
  }

  &__title {
    @include media-breakpoint-down(lg) {
      br {
        display: none;
      }
    }
  }

  &__field-text {
    @include media-breakpoint-down(md) {
      .field-text__input {
        height: 183px;
      }
    }

    margin-bottom: 0;
  }

  &__submit-btn {
    @include media-breakpoint-down(md) {
      display: none;
    }

    cursor: pointer;

    &::after {
      content: '';
      display: inline-block;
      width: 46px;
      height: 39px;
      background-image: url("data:image/svg+xml,%3Csvg width='47' height='40' viewBox='0 0 47 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.375' y='15.9644' width='38.5804' height='7.98216' fill='%23276EF1'/%3E%3Crect x='26.666' width='27.8713' height='7.98992' transform='rotate(45 26.666 0)' fill='%23276EF1'/%3E%3Crect x='46.375' y='19.8138' width='27.8713' height='7.99141' transform='rotate(135 46.375 19.8138)' fill='%23276EF1'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  .button {
    @include media-breakpoint-down(md) {
      display: block;
    }

    display: none;
  }
}
