.contacts-block {
  &__title {
    display: block;
    margin-bottom: 20px;
    padding-top: 30px;
    border-top: 1px solid $gray-lightest;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 800;
    font-size: 22px;
    line-height: 27px;
  }

  &__list {
    display: flex;
    gap: 30px;
  }

  &__item a {
    color: $color-main;
    font-weight: 900;
  }
}
