.work-history-tabs {
  $tabs-selector: '.tabs';

  #{$tabs-selector}__links {
    padding: 0;
    border-bottom: none;
  }

  #{$tabs-selector}__link {
    @include media-breakpoint-down(md) {
      padding: 20px 33px;
    }

    padding: 20px 50px;
  }

  #{$tabs-selector}__link-wrap--active {
    a {
      color: $white;
      background-color: $color-main;
    }
  }

  #{$tabs-selector}__content-wrapper {
    padding: 0;
  }
}
