.feedback-form {
  &__fields-wrap {
    @include media-breakpoint-down(md) {
      grid-template-columns: 1fr;
    }

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 75px);
    gap: 30px;
    margin-bottom: 25px;

    .field-text {
      margin-bottom: 0;

      &:last-child {
        @include media-breakpoint-down(md) {
          grid-column: auto;
          grid-row: auto;
        }

        grid-column: 2 / -1;
        grid-row: 1 / 4;
      }
    }
  }
}
