.field-text {
  display: block;
  margin-bottom: 30px;
}

.field-text__name {
  display: block;
  margin-bottom: 0.6em;
  color: $gray;
  line-height: 1.5;
}

.field-text__input-wrap {
  display: block;
}

.field-text__input {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  margin: 0;
  border: 4px solid transparent;
  padding: 6px 12px;
  line-height: 1.5;
  font-size: 1em;
  font-family: inherit;
  background-color: $gray-lightest;
  appearance: textfield;
}

.field-text__input:focus {
  @include field-focus();
}

.field-text__input:selection {
  box-shadow: none;
}

.field-text textarea {
  height: auto;
  resize: vertical;
  background-color: $gray-lightest;
}

.field-text__input::placeholder {
  font-family: '"Roboto"', sans-serif;
  font-weight: 400;
  line-height: 30px;
  color: $gray-light;
}

.field-text__help-text {
  display: block;
  padding-top: 0.4em;
  font-size: 0.75em;
  line-height: 1.2em;
}

.field-text__help-text:empty {
  display: none;
}

.field-text--error .field-text__input {
  border-color: $color-danger;
}
