// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {
  $component-name: &;

  position: sticky;
  top: 0;
  z-index: 20;

  color: #fff;
  font-weight: 500;

  background-color: #000;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: $header-height;
  }
}
