.reviews-item {
  margin-bottom: 30px;
  background-color: $gray-lightest;

  &:last-child {
    margin-bottom: 0;
  }

  &__inner {
    padding: 30px;
  }

  &__header {
    @include media-breakpoint-down(md) {
      flex-direction: column;
      text-align: center;
      margin-bottom: 25px;
    }

    display: flex;
    align-items: center;
    gap: 15px;

    margin-bottom: 15px;
    padding-bottom: 22px;
    border-bottom: 1px solid rgba($black, 0.1);
  }

  &__avatar {
    @include media-breakpoint-down(md) {
      width: 120px;
      height: 120px;

      img {
        width: 120px;
        height: 120px;
        object-fit: cover;
      }
    }

    border-radius: $border-radius;
    overflow: hidden;
  }

  &__user-info-wrap {
    display: flex;
    flex-direction: column;
  }

  &__username {
    @include media-breakpoint-down(md) {
      font-size: 21px;
      line-height: 25px;
    }

    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  &__date {
    @include media-breakpoint-down(md) {
      font-size: 19px;
      line-height: 24px;
    }

    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
  }

  &__text {
    margin-bottom: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
  }
}
