.transportations-search-form {
  $block-name: &;

  &__title {
    @include media-breakpoint-down(md) {
      margin-bottom: 30px;
    }
    margin-bottom: 45px;
  }

  &__fields-wrap {
    position: relative;

    #{$block-name}__field-text input {
      padding: 11px 35px;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 15px;

      width: 10px;
      height: 75px;
      transform: translateY(-50%);

      background: url('../images/top-section/from-to-line.svg') no-repeat;
      background-size: cover;
    }
  }

  &__field-text {
    margin-bottom: 6px;
  }

  &__actions {
    @include media-breakpoint-down(md) {
      row-gap: 10px;
    }
    display: grid;
    gap: 30px 14px;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 20px;
  }

  &__new-post-link {
    @include media-breakpoint-down(md) {
      gap: 10px;
      padding: 0;
      padding-top: 15px;
      padding-bottom: 15px;
    }
    padding-top: 0;
    padding-bottom: 0;
    color: $color-main;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 24px;

    &:hover {
      color: darken($color-main, 20%);
    }
  }

  &__filters-clear-btn {
    @include media-breakpoint-down(md) {
      gap: 10px;
      padding: 0;
      padding-top: 15px;
      padding-bottom: 15px;
    }
    padding-top: 0;
    padding-bottom: 0;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 24px;
  }

  &__submit-btn,
  &__calendar-btn {
    @include media-breakpoint-down(md) {
      grid-column: span 2;
    }
    padding-top: 1em;
    padding-bottom: 1em;
  }
}
